import OnRamperLogo from '@/assets/icons/onramper-logo.png';
import TransakLogo from '@/assets/icons/transak-logo.png';
export default {
  props: ['provider'],
  computed: {
    logo() {
      const logos = {
        onramper: OnRamperLogo,
        transak: TransakLogo
      };
      return logos[this.provider];
    }
  }
};