import Modal from '@/components/Modal';
import Logo from '@/components/icons/Logo';
import { version } from '/package.json';
import { mapActions, mapState } from 'vuex';
import ArrowLeftIcon from '@/assets/icons/arrow_left.svg';
import ArrowRightIcon from '@/assets/icons/arrow_right.svg';
export default {
  components: {
    Modal,
    ArrowRightIcon,
    ArrowLeftIcon,
    Logo
  },
  data: function () {
    return {
      open: false,
      currentView: 1,
      hasClapped: false,
      loading: false
    };
  },
  computed: {
    ...mapState(['whatsNewModalVersion', 'termsAcceptedAt', 'unlockedAt']),
    ...mapState('app', ['whatsNewModalContent']),
    appVersion() {
      return version;
    },
    carouselLines() {
      return this.whatsNewModalContent.length;
    }
  },
  methods: {
    ...mapActions(['setWhatsNewModalVersion']),
    close() {
      this.open = false;
    }
  },
  created() {
    if (this.whatsNewModalVersion !== this.appVersion || process.env.VUE_APP_SHOW_WHATS_NEW_ALWAYS) {
      this.open = true;
      this.setWhatsNewModalVersion({
        version: this.appVersion
      });
    }
  }
};